@font-face {
  font-family: 'Stolzl';
  src: local('Stolzl'),
    url('/fonts/Stolzl/Stolzl_Book.eot') format('eot'),
    url('/fonts/Stolzl/Stolzl_Book.woff') format('woff'),
    url('/fonts/Stolzl/Stolzl_Book.woff2') format('woff2');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

